.section__gallery {
  position: relative;
  top: 4rem;
  padding: 0 3rem;
  width: 100%;

  .gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin-top: 2rem;

    &__image {
      max-width: 500px;
      max-height: 500px;

      & > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}